import React, { createContext, useState, useContext, useEffect } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '../firebase';

const ProductContext = createContext();

export const useProducts = () => {
  return useContext(ProductContext);
};

export const ProductProvider = ({ children }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAllProducts = async () => {
      try {
        const productsCollection = collection(db, 'products');
        const q = query(productsCollection, where('visible', '==', true));
        const querySnapshot = await getDocs(q);
        const productsData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        
        setProducts(productsData);
        // Cache the fresh data
        sessionStorage.setItem('allProducts', JSON.stringify(productsData));

      } catch (error) {
        console.error("Error fetching all products:", error);
      } finally {
        // The loading state is primarily for the initial load without a cache.
        // Subsequent background fetches shouldn't trigger a global loading indicator.
        setLoading(false);
      }
    };

    // Check for cached data first to provide an instant experience
    const cachedProducts = sessionStorage.getItem('allProducts');
    if (cachedProducts) {
      try {
        const parsedProducts = JSON.parse(cachedProducts);
        setProducts(parsedProducts);
        setLoading(false); // We have data, so we are not "loading" from the user's perspective
      } catch (e) {
        // If parsing fails, remove the bad data to allow a clean fetch
        sessionStorage.removeItem('allProducts');
        console.error("Failed to parse cached products", e);
      }
    }

    // Always fetch fresh data in the background
    fetchAllProducts();
  }, []);

  const value = {
    products,
    loading,
  };

  return (
    <ProductContext.Provider value={value}>
      {children}
    </ProductContext.Provider>
  );
};